import Vue from 'vue'

Vue.mixin({
  data() {
    return {
      scrollPosition: window.scrollY
    }
  },
  methods: {
    get_scroll_position() {
      window.addEventListener('scroll', () => {
        this.scrollPosition = window.scrollY;
        // this.$store.state.scrollPosition = window.scrollY;
      });
      return this.scrollPosition
    }
  },
})
