    async updateFB({ dispatch,commit,getters}, { info, name}){
      console.log('updating FB name:',name)
      try{
        // const uid = await dispatch('fetchUserId')
        // const uid = firebase.auth().currentUser.uid
        console.log('updating FB info:',info)
        const uid = getters.userId
        await firebase
          .database()
          .ref(`/users/${getters.isAK?getIdAK(uid):uid}/info`)
          .child(name)
          .set(getters[info][name])
        if(name!=='historyList'){//не показывать сообщ об обновлении истории
          commit('activatePopupFB',{text: (getters.isAK?'ak ':'')+'updated'});
        }
      }catch(error){
        commit('activatePopupFB',{text:getters.isAK?'ak':''+' update failed:' + error});
        throw error
      }
    },

