import firebase from 'firebase/compat';
import router from '../router';


export default {
  actions: {
    fetchUserId({commit}){
      // const user = firebase.auth().currentUser
      // commit('setUserId',user ? user.uid : null);
      // return user ? user.uid : null
    },

    async login({ dispatch,commit},{email,password}){
      try{
        console.log('login')
        await firebase.auth().signInWithEmailAndPassword(email,password)
        await commit('activatePopupFB',{text:'login successful'});
      }catch(error){
        // commit('activatePopupFB',{text:error});
        commit('activatePopup', { text:'login failed'});
      }
    },

    async logout({ dispatch,commit }){
      try{
        await firebase.auth().signOut()
        commit('clearInfoAfterLogout')
        commit('activatePopupFB',{text:'logged out'});
      }catch(error){}
    },

    async register({ dispatch,commit },{email,password,name}){
      try{
        await firebase.auth().createUserWithEmailAndPassword(email,password)
        const uid = await dispatch('getUid')
        await firebase.database().ref(`/users/${uid}/info`).set({
          settings:{
            name,
            email,
            avaUrl:'',
          }
        })
        await commit('activatePopupFB',{text:'new user is registered'});
      }catch(error){
        // commit('setError',error)
        commit('activatePopupFB',{text:'reg is failed'});
        console.log('throw error:',error)
        throw error
      }
    },
    getUid(){
      const user = firebase.auth().currentUser
      console.log('got uid:',user ? user.uid : null)
      return user ? user.uid : null
    }

  }
}
