<template>
  <div class="alarmContainer" :class="{'open': isOpen}">
      <ul class="list">
        <router-link
          tag="li"
          to="alarm"
          v-for="item in cAlarmEventsListActivated"
          :key="item.id"
          class="listItem pr"
        >
          <button class="delBtn" @click="delItem(item.id,'alarmEventsList')">x</button>
          <div class="timeBox fz12 mr5">
            {{item.date}}
          </div>
          <div class="curr">{{item.comment}}</div>
        </router-link>
        <li class="listItem pr" v-if="$store.state.totalCoinsAmountCleaned>=295">
          редактировать loadMyGeckoCoins с 6 запросов на 7 и поменять alarm
        </li>
        <router-link
          to="alarm"
          tag="li"
          v-for="item in cAlarmListActivated"
          :key="item.id"
          class="listItem pr"
        >
          <button class="delBtn" @click="delItem(item.id,'alarmAltsList')">x</button>
          <div class="symbolBox">
            <img :src="item.coinUrl" style='width:15px' alt="coin ico"/>
          </div>
          <div class="coinBox">
            <a :href="makeUrl(item.coin)" target="_blank" style="text-decoration: none; color:unset">
              <div class="coin" style="font-size: 14px;">{{item.coin.toUpperCase()}}</div>
            </a>
          </div>
          <div class="coinBox mr5">
            <div class="numberBox number" style="font-size: 12px;">{{item.desiredPrice | numFormat('$ 0,0.00')}}</div>
          </div>
          <div class="coinBox">
            <div class="numberBox curr" style="font-size: 12px;">{{item.currentPrice | numFormat('$ 0,0.00')}}</div>
          </div>
          <div class="symbolBox alarmPic">
          </div>
          <br/>
          <div style="font-size: 12px; color:rgba(255, 255, 255, 0.4); width: 100%; text-align: center" >{{item.comment}}</div>
        </router-link>
        <li class="listItem pr">
          <span @click="isDropped=!isDropped" class="label hover">potentials desired prices:</span>
          {{cAlarmPotentialActivated.length}}
          <span class="label hover">items</span>
          <br/>
          <Transition name="fade">
            <ul v-if="isDropped" class="innerList">
              <FilterBlock items-name="exchanges" :list="cExchangeList" :selected-item.sync="selectedExchange"/>
              <li
                v-for="item in cAlarmPotentialActivated"
                :key="item.id"
                class="listItem"
              >
                <div class="symbolBox">
                  <img :src="item.coinUrl" style='width:15px' alt="coin ico"/>
                </div>
                <div class="coinBox">
                  <div class="name">{{item.name}}</div>
                  <div class="coin" style="font-size: 14px;">{{item.coin.toUpperCase()}}</div>
                </div>
                <div class="coinBox mr5">
                  <div class="numberBox number" style="font-size: 12px;">{{item.desiredPrice | numFormat('$ 0,0.00')}}</div>
                </div>
                <div class="coinBox">
                  <div class="numberBox curr" style="font-size: 12px;">{{item.currentPrice | numFormat('$ 0,0.00')}}</div>
                </div>
                <br/>
                <div style="font-size: 12px; color:rgba(255, 255, 255, 0.4); width: 330px; text-align: center" > {{item.comment}}</div>
              </li>
            </ul>
          </Transition>
        </li>
      </ul>
    <button type="button" class="btn hover" @click="isOpen=!isOpen" v-if="cAlarmEventsListActivated.length || cAlarmListActivated.length || cAlarmPotentialActivated.length">
      <img src="/img/alarmItem.png" style='width:100%' alt="alarm ico"/>
    </button>
  </div>

</template>

<script>
  import makeUrlFromExchangeName from '../../helpers/makeUrlFromExchangeName';
  import FilterBlock from '../AltsList/FilterBlock';

  export default {
    components: { FilterBlock },
    data(){
      return{
        isDropped:false,
        isOpen:true,
        selectedExchange:'all',
      }
    },
    computed: {
      cExchangeList(){
        return this.$store.state.infoMain.potentialList||[]
      },
      cAlarmListActivated(){
        if(this.$store.state.infoMain.alarmAltsList){
          return this.$store.state.infoMain.alarmAltsList.filter(item=>{
            if(item.amount<0){
              return item.currentPrice>=item.desiredPrice
            }else return item.currentPrice<=item.desiredPrice
          })
        }else return []
      },
      cAlarmEventsListActivated(){
        if(this.$store.state.infoMain.alarmEventsList){
          return this.$store.state.infoMain.alarmEventsList.filter(item=>new Date(item.date)<new Date(this.$store.state.updateDate))
        }else return []
      },
      cAlarmPotentialActivated(){
        let newList = this.$store.state.infoMain.potentialList;

        if (this.selectedExchange!=='all'){
          newList = newList.filter(item => item.exchange===this.selectedExchange)
        }
        return newList.filter(item=>item.currentPrice<item.desiredPrice)
      },
    },
    methods:{
      makeUrl(name){
        const coinObjInTradeList = this.$store.state.infoMain.tradeList.find(item=>item.coin === name);
        const coinObjInPotentialList = this.$store.state.infoMain.potentialList.find(item=>item.coin === name);
        const exObj=coinObjInTradeList||coinObjInPotentialList
        const ex=exObj.exchange
        return makeUrlFromExchangeName(ex,name)
      },
      delItem(id,list){
        const ok = confirm('r u sure?')
        if(ok){
          this.$store.commit('deleteItem', { id, listName:list})
        }
      },
    },
  }

</script>

