import Vue from 'vue';
import App from './App.vue';
import store from './store';
import numeral from 'numeral';
import Vuelidate from 'vuelidate/src'
import Paginate from 'vuejs-paginate'
import VueMeta from 'vue-meta'
import dateFilter from './filters/date.filter'
import localizeFilter from './filters/localize.filter'
import tooltipDirective from '@/directives/tooltip.directive'
import messagePlugin from '@/utils/message.plugin'
import titlePlugin from '@/utils/title.plugin'
import Msg from './components/Msg'
import MsgFB from './components/MsgFB'
import Loader from './components/app/Loader'
import numFormat from 'vue-filter-number-format';
import ToggleButton from 'vue-js-toggle-button';//https://github.com/euvl/vue-js-toggle-button
import Trend from "vuetrend";//https://thewebdev.info/2020/09/06/add-sparklines-to-our-vue-app-with-vue-trend/
import VueClipboard from 'vue-clipboard2';//https://www.npmjs.com/package/vue-clipboard2
import router from './router';
import Sticky from 'vue-sticky-directive';
// import vuetify from '@/plugins/vuetify' // path to vuetify export
// import 'materialize-css/dist/js/materialize.min'
import CircularCountDownTimer from "vue-circular-count-down-timer";//https://vuejsexamples.com/a-count-down-timer-for-vue-js-framework/
import VueScrollReveal from 'vue-scroll-reveal';
import firebase from 'firebase/compat';
import 'firebase/auth';
import 'firebase/database';
import clickOutside from './directives/clickOutside';
import scrolldo from './directives/scrolldo';

Vue.config.productionTip = false;

Vue.directive('tooltip',tooltipDirective)
Vue.directive('clickOutside',clickOutside)
Vue.directive('scrolldo',scrolldo)
Vue.filter('localize', localizeFilter)
Vue.use(CircularCountDownTimer);
Vue.use(messagePlugin)
Vue.use(Vuelidate)
Vue.use(VueScrollReveal);
Vue.use(VueMeta)
Vue.use(titlePlugin)
Vue.component('paginate', Paginate)
// Vue.component('Loader',Loader)
Vue.component('Msg',Msg)
Vue.component('MsgFB',MsgFB)
Vue.filter('date', dateFilter)
Vue.filter('localize', localizeFilter)
Vue.filter('numFormat', numFormat(numeral));
Vue.use(VueClipboard);
Vue.use(Trend);
Vue.use(ToggleButton);
Vue.use(Sticky);
firebase.initializeApp({//подключ к базе данных
  apiKey: process.env.VUE_APP_FIREBASE_API_KEY,
  authDomain: process.env.VUE_APP_FIREBASE_AUTH_DOMAIN,
  databaseURL: process.env.VUE_APP_FIREBASE_DB_URL,
  projectId: process.env.VUE_APP_FIREBASE_PROJECT_ID,
  storageBucket: process.env.VUE_APP_FIREBASE_STORAGE_BUCKET,
  messagingSenderId: process.env.VUE_APP_FIREBASE_MESSAGING_SENDER_ID,
  appId: process.env.VUE_APP_FIREBASE_API_ID
});

let app;

firebase.auth().onAuthStateChanged(()=>{//если авторизован то рендер всего приложения
  if(!app){//избежать рендера кажд раз при логине и логауте. Инициализировано ли уже приложение?
    app = new Vue({
      router,
      store,
      render: (h) => h(App),
    }).$mount('#app');
  }
  // createApp(App).use(store).use(router).mount('app')
})
