  <section>
    <SumContainer class="pr">
      <div class="sumBlock">
        <div class="sumLabel fc"  v-if="!short">invested</div>
        <div class="sumLabelHyp fc"  v-if="short">{{title}}</div>
        <div class="sumNumber fc" v-if="!short">{{totalInvested| numFormat('$ 0,0')}}</div>
      </div>
      <div class="sumBlock">
        <div class="sumLabel fc" v-if="!short">p/l</div>
        <div class="sumNumber fc" :class="{'green':profit>0, 'red':profit<0}">
          <span v-if="profit>0"> +</span>
          {{profit| numFormat('0,0')}}
        </div>
      </div>
      <div class="sumBlock">
        <div class="sumLabel fc"  v-if="!short">now</div>
        <div class="sumNumber fc">= {{totalNow| numFormat('0,0')}}</div>
      </div>
  <!--    <div class="sumBlock">-->
  <!--      <div class="sumLabel fc">compound p/l</div>-->
  <!--      <div class="sumNumber fc" :class="{'green':compoundProfit>0, 'red':compoundProfit<0}">{{compoundProfit| numFormat('$ 0,0')}}</div>-->
  <!--    </div>-->
      <div class="sumBlock hideMob" v-if="$route.name!=='trash' && !short">
        <div class="sumLabel fc">X's</div>
        <div class="sumNumber fc">{{xs| numFormat('0,0.0')}}</div>
      </div>
      <div class="sumBlock hideMob" v-if="$route.name!=='trash' && $route.name!=='hyp' && !short">
        <div class="sumLabel fc">fixed p/l</div>
        <div class="sumNumberSmaller fc">{{fixedProfit| numFormat('$ 0,0')}}</div>
  <!--      <div class="sumNumberSmaller fc" :class="{'green':fixedProfit>0, 'red':fixedProfit<0}">{{fixedProfit| numFormat('$ 0,0')}}</div>-->
      </div>
      <div class="sumBlock" v-if="short">
        <div class="sumNumberDiff fc" :class="{'green':profit<0, 'red':profit>0}">{{(sumNow.totalNow-totalNow)| numFormat('0,0')}}</div>
      </div>
      <div class="sumBlock" v-if="($route.name==='main' || $route.name==='reserve') && !short">
        <div class="sumLabel fc">bad trades</div>
        <div class="sumNumberSmaller fc red">{{-1*totalLoss| numFormat('$ 0,0')}}</div>
      </div>
<!--      <div class="sumBlock" v-if="$route.name==='main' || $route.name==='reserve'">-->
<!--        <div class="sumLabel fc">my expenses</div>-->
<!--        <div class="sumNumberSmaller fc red">{{-1*totalExpenses| numFormat('$ 0,0')}}</div>-->
<!--      </div>-->
      <div class="sumBlock" v-if="($route.name==='main' || $route.name==='reserve') && !short">
        <div class="sumLabel fc" v-if="$store.state.userId[0]==='4'">{{!$store.state.isAK?'K':'A'}} now</div>
        <div class="sumLabel fc" v-if="$store.state.userId[0]!=='4'">{{!$store.state.isAK?'A':'K'}} now</div>
        <div class="sumNumber fc grey">{{totalNowAK| numFormat('$ 0,0')}}</div>
      </div>
      <div class="sumBlock" v-if="($route.name==='main' || $route.name==='reserve') && !short">
        <div class="sumLabel fc">fund</div>
        <div class="sumNumber fc grey" v-if="$store.state.userId[0]!=='4'">{{$store.state.infoAK.fundManualTotal| numFormat('$ 0,0')}}</div>
        <input
          v-if="$store.state.userId[0]==='4'"
          type="text"
          v-model.number='$store.state.infoMain.fundManualTotal'
          class="fundInput sumNumber fc grey"
          v-on:keyup.enter="(ev) => {ev.target.blur();update()}"
        >
      </div>
      <div class="sumBlock" v-if="$route.name!=='ak' && $route.name!=='trash' && $route.name!=='hyp' && !short">
        <div class="sumLabel fc">total now</div>
        <div class="sumNumber fc grey">{{compoundTotalNow| numFormat('$ 0,0')}}</div>
      </div>
    </SumContainer>
    <div v-if="!short">
      <div class="fc">
        <SumContainer class="mr10" style="align-items: center" v-if="$route.name==='main'">
          <a href="https://alternative.me/crypto" target="_blank">
            <div class="shade"/>
            <img src="https://alternative.me/crypto/fear-and-greed-index.png"
                 alt=" Fear & Greed Index"
                 style="width:100px"
                 :key="componentKeyForForceRerender"
            />
          </a>
        </SumContainer>

        <SumContainer style="align-items: center" v-if="!short">
          <SumItem label="BTC meter" :number="btcMeter | numFormat('0,0.000')" id="btcMeter"/>
          <SumItem label="Eth meter" :number="ethMeter | numFormat('0,0.0')" id="ethMeter" />
        </SumContainer>
        <SumContainer class="ml10" style="align-items: center" v-if="$route.name==='main'">
          <div>
            <img :src="btc.image" style='width:10px' alt="coin ico"/>
            <div class="number ml5" style='display: inline'>{{btc.current_price | numFormat('0,0')}}</div>
          </div>
          <span class="ml5 label"> / </span>
          <div>
            <img :src="eth.image" style='width:10px' alt="coin ico"/>
            <div class="number ml5" style='display: inline'>{{eth.current_price | numFormat('0,0')}}</div>
          </div>
          <span class="ml5 label mr5"> = </span>
          <div>
            <div class="number" style='display: inline-block'>{{ethInBtc | numFormat('0,0.0')}}</div>
          </div>
        </SumContainer>
      </div>
      <router-link to="history" >
        <div class="fc" v-if="$route.name==='main'">
          <div class="hAmount">{{cHistoryListForTrend.length}}</div>
            <trend
              :data="cHistoryListForTrend"
              :gradient="['green', 'white', 'red']"
              :autoDrawDuration="700"
              :stroke-width="2"
              smooth
              :width="700"
              :height="150"
              v-if="$route.name!=='hyp' && $route.name!=='reserves'"
              class="card"
            ></trend>
        </div>
      </router-link>
      </div>
  </section>
</template>

<script>
  import SumContainer from './SumContainer';
  import generateRandomId from '../../helpers/generateRandomId';
  import SumItem from './SumItem';
  export default {
    props:['list','myGeckoList','short','title','noUpdate', 'sumNow'],
    data() {
      return {
        radio: true,
        componentKeyForForceRerender: 0,
      };
    },

    components: {
      SumItem,
      SumContainer,
    },
    methods: {
      update(){
        this.$store.dispatch('updateFB',{name:'fundManualTotal',info:this.$store.state.isAK?'infoAK':'infoMain'});
      },
    },
    computed: {
      totalInvested(){
        if(this.list&&this.list.length){
          return this.list.map(item=>item.totalAmount*item.averagePrice).reduce((a, b) => a+b)
        }else return 0
      },
      profit(){
        if(this.list&&this.list.length) {
          return this.list.map(item=>item.profit).reduce((a, b) => a+b);
        }else return 0
      },
      totalNow(){
        return this.totalInvested + this.profit;
      },
      totalNowAK(){
        if(this.$store.state[this.$store.state.isAK?'infoMain':'infoAK'].tradeList){
          return this.$store.state[this.$store.state.isAK?'infoMain':'infoAK'].tradeList.map(item=> (item.totalAmount)*item.currentPrice).reduce((a, b) => a+b)
        }else return 0
      },
      compoundTotalNow(){
        if(this.$store.state.infoMain.fundManualTotal){
          return this.totalNow+this.totalNowAK+this.$store.state.infoMain.fundManualTotal
        }
      },
      fixedProfit(){
        if(this.list&&this.list.length) {
          return this.list.map(item=>item.fixedProfit).reduce((a, b) => a+b);
        }
        if(this.list&&!this.list.length) return 0
        if(this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'].tradeList&&this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'].tradeList.length){
          return this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'].tradeList.map(item=>item.fixedProfit).reduce((a, b) => a+b);
        }else return 0
      },
      // totalExpenses(){
      //   const expenses = this.totalInvested - this.fixedProfit;
      //   if(expenses<0){
      //     return 0
      //   }else {
      //     return expenses*(-1)//чтобы было красное отрицательное
      //   }
      // },
      compoundProfit(){
        return this.fixedProfit + this.profit;
      },
      xs() {
        if(this.totalInvested===0){
          return 0
        }
        return this.totalNow / this.totalInvested;
      },
      xsCompound(){
        if(this.totalInvested===0){
          return 0
        }
        return (this.totalNow+this.fixedProfit)/this.totalInvested;
      },
      btcMeter(){
        if(this.$store.state.myGeckoList&&this.$store.state.myGeckoList.length) {
          const coinObjInGecko = this.$store.state.myGeckoList.find(item=>item.symbol === 'btc');
          const curr = coinObjInGecko.current_price;
          return this.totalNow / curr;
        }else return 0
      },
      ethMeter(){
        if(this.$store.state.myGeckoList&&this.$store.state.myGeckoList.length) {
          const coinObjInGecko = this.$store.state.myGeckoList.find(item=>item.symbol === 'eth');
          const curr = coinObjInGecko.current_price;
          return this.totalNow / curr;
        }else return 0
      },
      btc(){
        if(this.$store.state.myGeckoList&&this.$store.state.myGeckoList.length) {
          return this.$store.state.myGeckoList.find(item=>item.symbol === 'btc');
        }else return []
      },
      eth(){
        if(this.$store.state.myGeckoList&&this.$store.state.myGeckoList.length) {
          return this.$store.state.myGeckoList.find(item=>item.symbol === 'eth');
        }else return []
      },
      ethInBtc(){
        if(this.btc&&this.eth) {
          return this.btc.current_price/this.eth.current_price;
        }else return 0
      },
      totalLoss(){
        if(this.list &&this.list.length){
          const loseCoinsArr = this.list.filter(item=>item.profit<0);
          if(loseCoinsArr.length===0){
            return 0
          }else {
            return loseCoinsArr.map(item=>item.profit).reduce((a, b) => a+b);
          }
        }
      },
      //------------------------------------------------------------------------------
      cHistoryListForTrend(){
        if(this.$route.name==='main' && this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'].historyList){
          return this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'].historyList.map(item=>item.totalNow)
        } else if(this.list&&this.list.length){
          return this.list.map(item=>item.totalNow)
        } else return []
      },
    },
    watch: {
      totalNow() {//при изменении значения записывать в историю в локал. Квота на лимит в сторе, поэтому условие редкости.
        if(this.$route.name!=='main'){return}
        if(this.noUpdate){return}
        const item = {//при первнач рендере идет запоздалая загрузка геко и поэтому вверху везде проставлены условия this.$store.state.myGeckoList.length
          totalExpenses: this.totalExpenses?+this.totalExpenses.toFixed(3):0,
          totalInvested: this.totalInvested?+this.totalInvested.toFixed(3):0,
          totalNow: this.totalNow?+this.totalNow.toFixed(3):0,
          profit: this.profit?+this.profit.toFixed(3):0,
          fixedProfit: this.fixedProfit?+this.fixedProfit.toFixed(3):0,
          compoundProfit: this.compoundProfit?+this.compoundProfit.toFixed(3):0,
          xs: this.xs?+this.xs.toFixed(3):0,
          xsCompound: this.xsCompound?+this.xsCompound.toFixed(3):0,
          btcMeter: this.btcMeter?+this.btcMeter.toFixed(3):0,
          ethMeter: this.ethMeter?+this.ethMeter.toFixed(3):0,
          ethInBtc: this.ethInBtc?+this.ethInBtc.toFixed(3):0,
          totalLoss: this.totalLoss?+this.totalLoss.toFixed(3):0,
          date: new Date().toLocaleString('en-US'),
          id: generateRandomId(),
        }
        this.$store.commit('addHistoryItem', item)
        this.$store.commit('setTotal', item)
      }
    }
  }
</script>

