<script>
  import clickOutside from '../../directives/clickOutside'
  import {email,required,minLength} from 'vuelidate/lib/validators'
  import UserBlock from '../UserBlock';
  import SideLogin from '../Beloe/SideLogin';
  export default {
    components: { UserBlock, SideLogin },
    directives: {clickOutside},
    data: () => ({
      isRegisterOpen:false,
      dateSF: null,
      dateMS: null,
      dateEST: null,
      dateUTC: null,
      intervalAlarm:null,
      interval:null,//in mounted we record this interval
      dropdown:null,
      isMenuOpen:false,
      isMenuOpenByButton:false,//фикс закрывания сразу
      email:'',
      password:'',
      name:'',
      isOpenSide:false,
      loginForm:{
        email:null,
        password:null
      },
      registerForm:{
        email:null,
        password:null,
        name:null
      },
    }),
    validations:{
      email:{email,required},
      password: {required, minLength: minLength(6)},
      name:{required},
    },
    methods:{
      logout(){
        this.$store.dispatch('logout')
        this.$router.push('/login')
      },
      toOpen(){
        this.isMenuOpen = !this.isMenuOpen
        this.isMenuOpenByButton = true//фикс закрывания сразу
      },
      toClose(){
        this.isMenuOpen = false
        this.isMenuOpenByButton = false
      },
      onClickOutside (event) {
        if(this.isMenuOpen && !this.isMenuOpenByButton){
          return this.isMenuOpen = !this.isMenuOpen;
        }
        this.isMenuOpenByButton = false//фикс закрывания сразу. Возврат в первонач состояние.
      },
      async submitHandler(){
        console.log(this.$v.password.$params.minLength)
        if(this.$v.$invalid){
          this.$v.$touch()
          return
        }
        const formData = {
          email: this.email,
          password: this.password,
          name:this.name
        }
        try{
          console.log('dispatching register:',formData)
          await this.$store.dispatch('register',formData)
          // this.$router.push('/')
        }catch(e){}
      }

    },
    computed:{
      computedNavItemsArr(){
        return [
          {title:'MyCrypto', linkName:'/'},
          {title:'Home', linkName:'beloe'},
          {title:'About', linkName:'beloe/about'},
          {title:'Service', linkName:'beloe/service'},
          {title:'Training', linkName:'beloe/training'},
          {title:'Events', linkName:'beloe/events'},
          {title:'Feedback', linkName:'beloe/feedback'},
        ]
      },
    },
    mounted () {
      this.interval = setInterval(()=>{
        this.dateSF= new Date().toLocaleString("ru-Ru", {timeZone: "America/los_angeles"})
        this.dateMS= new Date().toLocaleString("ru-RU", {timeZone: "Europe/Moscow"})
        this.dateEST= new Date().toLocaleString("ru-RU", {timeZone: "EST"})
        this.dateUTC= new Date().toLocaleString("ru-RU", {timeZone: "UTC"})
      },1000)

      // this.dropdown = M.Dropdown.init(this.$refs.dropdown,{
      //   constrainWidth:true
      // })
    },
    watch: {//при ручном изменении урла срабатывает рендер. При первом рендере и при послед изменениях.
      '$route.name': {
        handler(){//завод опция "что сделать"
          this.toClose();
        },
        immediate:false,//= метод created() сработает с таким же содержимым (при первом рендере). Можно удалить оригинальный метод created() .
      },
    },
    beforeDestroy () {//очистить обновление времени из памяти при исчезновении панели Navbar
      clearInterval(this.interval)
    }
  }
</script>
