import splitArrayIntoChunksOfLen from '../helpers/splitArrayIntoChunksOfLen';
import axios from 'axios';
import generateRandomId from '../helpers/generateRandomId';

export default {
  state: {
    totalCoinsAmount:null,
    totalCoinsAmountCleaned:null,
    totalCoinsReceived:null,
  },
  getters:{
  },

  actions: {//опция actions где возможны синхр и ассинхр операции. Запуск происходит из компонента App.
    loadMyGeckoCoins({ dispatch, commit, getters, rootGetters },currency='usd') {//загрузить инфо только моих монет
      this.state.isLoading = true;
      if(!this.state.infoMain.tradeList){ // если нечего перебирать то отбой
        console.log('loadMyGeckoCoins. tradeList empty. ignore')
        this.state.isLoading = false;
        return
      }

      //сбор всех моих id
      // console.log('getting ids T:',this.state.infoMain.tradeList.length,'P:',this.state.infoMain.potentialList.length)
      // console.log('getting K ids T:',this.state.infoAK.tradeList.length,'P:',this.state.infoAK.potentialList.length)
      const tradeList = this.state.infoMain.tradeList.map(item=>item.id);
      const tradeListAK = this.state.infoAK&&this.state.infoAK.tradeList?this.state.infoAK.tradeList.map(item=>item.id):[];
      const potential = this.state.infoMain.potentialList.map(item=>item.id);
      const potentialAK = this.state.infoAK&&this.state.infoAK.potentialList?this.state.infoAK.potentialList.map(item=>item.id):[];
      const trash = this.state.infoMain.trashList.map(item=>item.id);
      const trashAK = this.state.infoAK&&this.state.infoAK.trashList?this.state.infoAK.trashList.map(item=>item.id):[];
      const all = [...tradeList,...tradeListAK,...potential,...potentialAK,...trash,...trashAK];//обьединяем все id в один массив
      const allCleaned = [...new Set(all)];//почистить массив от повторов если они имеются
      this.state.totalCoinsAmount = all.length;
      this.state.totalCoinsAmountCleaned = allCleaned.length;

      const splitedArr = splitArrayIntoChunksOfLen(allCleaned,50); //делим на порции ибо CoinGecko шлет макс 50 за один запрос

      const idsString0 = splitedArr[0].join('%2C');//создаем строку для запроса
      const idsString1 = splitedArr[1]?splitedArr[1].join('%2C'):'';//создаем строку для запроса
      const idsString2 = splitedArr[2]?splitedArr[2].join('%2C'):'';//создаем строку для запроса
      const idsString3 = splitedArr[3]?splitedArr[3].join('%2C'):'';//создаем строку для запроса
      const idsString4 = splitedArr[4]?splitedArr[4].join('%2C'):'';//создаем строку для запроса
      const idsString5 = splitedArr[5]?splitedArr[5].join('%2C'):'';//создаем строку для запроса

      //создаем запрос
      let valuesArr = [
        axios.get(`https://api.coingecko.com/api/v3/coins/markets?vs_currency=${currency}&ids=ethereum%2Cbitcoin%2C${idsString0}&order=market_cap_desc&per_page=50&page=1&sparkline=true`),
        axios.get(`https://api.coingecko.com/api/v3/coins/markets?vs_currency=${currency}&ids=${idsString1}&order=market_cap_desc&per_page=50&page=1&sparkline=true`),
        axios.get(`https://api.coingecko.com/api/v3/coins/markets?vs_currency=${currency}&ids=${idsString2}&order=market_cap_desc&per_page=50&page=1&sparkline=true`),
        axios.get(`https://api.coingecko.com/api/v3/coins/markets?vs_currency=${currency}&ids=${idsString3}&order=market_cap_desc&per_page=50&page=1&sparkline=true`),
        axios.get(`https://api.coingecko.com/api/v3/coins/markets?vs_currency=${currency}&ids=${idsString4}&order=market_cap_desc&per_page=50&page=1&sparkline=true`),
        axios.get(`https://api.coingecko.com/api/v3/coins/markets?vs_currency=${currency}&ids=${idsString5}&order=market_cap_desc&per_page=50&page=1&sparkline=true`),
      ]
      // //сокращаем число запросов с пустыми полями... Не получается убирать лишний ответ в spread'e
      // if(arr1.length===0){valuesArr.splice(1, 10)}
      // if(arr2.length===0){valuesArr.splice(2, 10)}
      // if(arr3.length===0){valuesArr.splice(3, 10)}
      // if(arr4.length===0){valuesArr.splice(4, 10)}
      axios.all(valuesArr)
        .then(axios.spread((res0, res1,res2,res3,res4,res5) => {
          this.state.myGeckoList = [...res0.data,...res1.data,...res2.data,...res3.data,...res4.data,...res5.data];//запихиваем все результаты моих альтов в стейт
          // console.log('answer this.state.myGeckoList:',this.state.myGeckoList)
          this.state.totalCoinsReceived = this.state.myGeckoList.length;
          commit('updateListWithMyGecko', {listName:'tradeList'})
          commit('updateListWithMyGecko', {listName:'tradeList',info:'infoAK'})
          commit('updateListWithMyGecko', {listName:'trashList'})
          commit('updateListWithMyGecko', {listName:'hypotheticalLists'})
          commit('updateListWithMyGecko', {listName:'lastActivityList'})
          commit('updatePotentialListWithMyGecko', 'infoMain')
          commit('updatePotentialListWithMyGecko', 'infoAK')
          // context.commit('updateListWithMyGecko', {listName:'potentialList'})
          // context.commit('updateListWithMyGecko', {listName:'potentialList',info:'infoAK'})
          commit('updateAlarmAltsListWithMyGecko', 'infoMain')
          commit('updateAlarmAltsListWithMyGecko', 'infoAK')
        }))
        .catch((err) => {
          console.log(err)
          commit('activatePopup',{text:'loadMyGeckoCoins' + err});
        })
        .then(()=>{
          this.state.isLoading = false;
        })
    },
    loadOnlyProvidedCoins({ dispatch, commit, getters, rootGetters },{list}) {//загрузить инфо только моих монет
      this.state.isLoading = true;
      const ids = list.map(item=>item.id);
      const all = [...ids];//обьединяем все id в один массив
      const allCleaned = [...new Set(all)];//почистить массив от повторов если они имеются
      const splitedArr = splitArrayIntoChunksOfLen(allCleaned,50); //делим на порции ибо CoinGecko шлет макс 50 за один запрос
      const idsString0 = splitedArr[0].join('%2C');//создаем строку для запроса
      const idsString1 = splitedArr[1]?splitedArr[1].join('%2C'):'';//создаем строку для запроса
      const idsString2 = splitedArr[2]?splitedArr[2].join('%2C'):'';//создаем строку для запроса

      //создаем запрос
      let valuesArr = [
        axios.get(`https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&ids=${idsString0}&order=market_cap_desc&per_page=50&page=1&sparkline=true`),
        axios.get(`https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&ids=${idsString1}&order=market_cap_desc&per_page=50&page=1&sparkline=true`),
        axios.get(`https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&ids=${idsString2}&order=market_cap_desc&per_page=50&page=1&sparkline=true`),
      ]
      return axios.all(valuesArr)
        .then(axios.spread((res0, res1,res2) => {
          const answer = [...res0.data,...res1.data,...res2.data]
          this.state.refreshedReserveTradeList = list.map(item => {
            const coinObjInGecko = answer.find(geckoitem => geckoitem.symbol === item.coin);
            if (coinObjInGecko) {//если найдена моя монета в моей базе то дополняем сущ лист ценами с базы. Если не найдена то ничего не дел.
              // console.log('addFreshPrices, if coinObjInGecko:', coinObjInGecko);
              return {
                ...item,
                currentPrice: coinObjInGecko.current_price,//кажд раз обновление.
                priceChangePercentage24h: coinObjInGecko.price_change_percentage_24h / 100,//кажд раз обновление.
                valueNow: +(item.totalAmount * coinObjInGecko.current_price).toFixed(5),//кажд раз пересчет.
                profit: +(item.totalAmount * coinObjInGecko.current_price).toFixed(5) - item.invested,//кажд раз пересчет.
                marketCapRank: coinObjInGecko.market_cap_rank,
                sparklineIn7d: coinObjInGecko.sparkline_in_7d.price,
              }
            } else return item
          })
            // console.log('answer',this.state.temporaryNewTradeListForReserve)
          }))
        .catch((err) => {
          console.log(err)
          commit('activatePopup',{text:'loadMyGeckoCoins' + err});
        })
        .then(()=>{
          this.state.isLoading = false;
        })
    },
    loadAllGeckoCoins({ dispatch, commit, getters, rootGetters }){
      return axios.get('https://api.coingecko.com/api/v3/coins/list')
        .then((res)=>{
          // console.log('loadAllGeckoCoins:',res.data.length)
          this.state.allCoins = res.data;
        })
        .catch((err) => {
          console.log('errorRRR from actions loadAllGeckoCoins:', err);
          commit('activatePopup', { text:'allcoins adding error'});
        })
    },
    loadGlobal({ dispatch, commit, getters, rootGetters }){
      return axios.get('https://api.coingecko.com/api/v3/global')
        .then((res)=>{
          // console.log('loadAllGeckoCoins:',res.data.length)
          this.state.global = res.data.data;
        })
        .catch((err) => {
          console.log('errorRRR from actions loadGlobal:', err);
          commit('activatePopup', { text:'loadGlobal error'});
        })
    },
    loadAlt({ dispatch, commit, getters, rootGetters },payload) {
      const isCoinInBase = this.state.allCoins.find(item => item.symbol === payload.coin);
      const isCoinInMyTradeList = this.state.infoMain.tradeList.find(item => item.coin === payload.coin);
      const isCoinInMyPotentialList = this.state.infoMain.potentialList.find(item => item.coin === payload.coin);
      const isCoinInMyTrashList = this.state.infoMain.trashList.find(item => item.coin === payload.coin);
      const btcPrice = this.state.myGeckoList.find(item=>item.symbol==='btc').current_price
      if (isCoinInMyTradeList && !payload.hypListId) {
        console.log('already in tradelist')
        return commit('addTrade', payload)
      } else if (!isCoinInBase && payload.manualId==='') {
        return alert('Нет такой монеты в базе')
      } else if(payload.amount<0){
        return alert('Продажа несуществующей монеты. Игнор.')
      }else{
        let coinId;
        if(payload.manualId!==''){
          coinId = payload.manualId
        }else if(isCoinInMyTrashList){
          coinId = isCoinInMyTrashList.id
        }else coinId = isCoinInBase.id
        this.state.isLoadingTrade=true;
        return axios.get(`https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&ids=${coinId}&order=market_cap_desc&per_page=250&page=1&sparkline=true`)
          .then((res) => {
            const item = {
              id: res.data[0].id,
              exchange: payload.exchange,
              exchangeUrl: `/img/${payload.exchange}.jpg`,
              coin: payload.coin,
              coinUrl: res.data[0].image,
              totalAmount: payload.amount,
              averagePrice: payload.price,
              currentPrice: res.data[0].current_price,
              invested: payload.amount * payload.price,
              valueNow: payload.amount * res.data[0].current_price,
              profit: payload.amount * res.data[0].current_price - payload.amount * payload.price,
              fixedProfit: 0,
              xs: 1,
              comment: payload.comment,
              name: res.data[0].name,
              sparklineIn7d: res.data[0].sparkline_in_7d.price,
              trades: [
                {
                  amount: payload.amount,
                  price: payload.inBtc?payload.price*btcPrice:payload.price,
                  comment: payload.comment,
                  id: payload.id,
                  date: payload.date,
                },
              ],
            }
            if(payload.hypListId){//значит нажата кнопка перенести в определенный гипот лист
              console.log('payload.hypListId',payload.hypListId)
              const tabObj = this.state[this.state.isAK?'infoAK':'infoMain'].hypotheticalLists.find(item=>item.id===payload.hypListId)
              console.log('tabObj',tabObj)
              if(!tabObj.hypTradeList){
                tabObj.hypTradeList = [item]
              }else tabObj.hypTradeList.push(item)
              commit('updateListWithMyGecko', {listName:'hypotheticalLists', info:this.state.isAK?'infoAK':'infoMain'})
              dispatch('updateFB',{name:'hypotheticalLists',info:this.state.isAK?'infoAK':'infoMain'});
            }else{
              this.state.infoMain.tradeList.push(item)//learn.epam.com. luxoft
              this.state.infoMain.lastActivityList.unshift(//добавляем activity запись в начало
                {
                  amount:payload.amount,
                  id:generateRandomId(),
                  date:payload.date,
                  comment:payload.comment,
                  coin:payload.coin,
                  price:payload.price,
                  currentPrice:res.data[0].current_price,
                  coinUrl:res.data[0].image,
                  name:res.data[0].name,
                }
              )
              commit('updateTradeListWithMyGecko', this.state.isAK?'infoAK':'infoMain')
              commit('calculate',{coin:payload.coin});
              dispatch('updateFB',{name:'tradeList',info:this.state.isAK?'infoAK':'infoMain'});
              dispatch('updateFB',{name:'lastActivityList',info:this.state.isAK?'infoAK':'infoMain'});
            }
            commit('activatePopup', { text:'trade coin added'});
          })
          .catch((err) => {
            console.log('errorRRR from actions loadAlt:', err);
          })
          .then(()=>{
            this.state.isLoadingTrade=false;
          })
      }
    },
    loadPotentialAlt(context, {potentialItem,info, listName}){//добавление нов монеты
      const isCoinInBase = this.state.allCoins.find(item => item.symbol === potentialItem.coin);
      const isCoinInMyTradeList = this.state[info].tradeList.find(item => item.coin === potentialItem.coin);
      const isCoinInMyPotentialList = this.state[info].potentialList.find(item => item.coin === potentialItem.coin);
      if(!isCoinInBase && potentialItem.manualId===''){
        alert('Нет такой монеты в базе')
        return
      }
      if((isCoinInMyPotentialList && potentialItem.manualId==='') || (isCoinInMyTradeList && potentialItem.manualId==='')){
        alert('Эта монета уже есть в списках. Игнор')
        return
      }
      let coinId;
      if(potentialItem.manualId!==''){
        coinId = potentialItem.manualId
      }else {
        coinId = isCoinInBase.id
      }
      this.state.isLoadingPotential = true;
      return axios.get(`https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&ids=${coinId}&order=market_cap_desc&per_page=250&page=1&sparkline=true`)
        .then((res) => {
          const item ={
            id: res.data[0].id,
            exchange: potentialItem.exchange,
            exchangeUrl: `/img/${potentialItem.exchange}.jpg`,
            coin: potentialItem.coin,
            coinUrl: res.data[0].image,
            desiredPrice:potentialItem.desiredPrice,
            currentPrice: res.data[0].current_price,
            name: res.data[0].name,
            priceChangePercentage24h:res.data[0].price_change_percentage_24h/100,
            sparklineIn7d: res.data[0].sparkline_in_7d.price,
            date:potentialItem.date,
          }
          this.state[info][listName].push(item)
          context.dispatch('updateFB',{name:listName,info:context.getters.isAK?'infoAK':'infoMain'});
          context.commit('activatePopup', { text:listName +' coin added'});
        })
        .catch((err) => {
          console.log('errorRRR from actions loadPotentialAlt:', err);
        })
        .then(()=>{
          this.state.isLoadingPotential = false;
        })
    },  }
}
