<template>
  <section>
    <form @submit.prevent="isRegisterForm?toRegister():toLogin()" style="text-align: center" v-if="!$store.state.beloeInfo">
      <div class="fb fz14 mb10">
        <label for="email">Email</label>
        <input
          id="email"
          type="text"
          v-model.trim="form.email"
          class="ml20 input"
        >
      </div>
      <div class="fb fz14 mb10">
        <label for="password">Password</label>
        <input
          id="password"
          type="password"
          v-model.trim="form.password"
          class="ml20 input"
          placeholder="6 digits"
        >
      </div>
      <div class="fb fz14 mb10" v-if="isRegisterForm">
        <label for="name">Name</label>
        <input
          id="name"
          type="text"
          v-model.trim="form.name"
          class="ml20 input"
        >
      </div>
      <button style="display: block; margin:20px auto" class="loginBtn" type="submit">{{isRegisterForm?'register':'login'}}</button>
      <span v-if="isRegisterForm">Have account? </span><span class="backLink" v-if="isRegisterForm" @click="isRegisterForm=false"> to login</span>
      <span v-if="!isRegisterForm" class="backLink" @click="isRegisterForm=true">to register</span>
      <br/>
    </form>
    <div class="fc">
      <span v-if="$store.state.beloeInfo" class="backLink" @click="toLogout">logout</span>
    </div>
<!--      <br/>-->
<!--      <span  class="backLink" @click="$store.dispatch('getUid')">console user</span>-->
  </section>
</template>

<script>
  export default {
    props:['open'],
    data() {
      return {
        form:{
          email:null,
          password:null,
          name:null
        },
        isRegisterForm:true
      };
    },
    methods: {
      async toLogin(){
        try{
          await this.$store.dispatch('login',this.form);
          this.$router.go()
        }catch(error){}
      },
      async toLogout(){
        try{
          await this.$store.dispatch('logout');
          this.$router.go()
        }catch(error){}
      },
      async toRegister(){
        try{
          await this.$store.dispatch('register',this.form);
          this.form.email=null;
          this.form.password=null;
          this.form.name=null;
          this.isRegisterForm=false;
          await this.$emit('update:open', false)
        }catch(error){}
          // await this.$store.commit('activatePopupFB',{text:'toRegister is failed'});
        // console.log('catch error')
      },
    },
  };
</script>


