<template>
  <div>
    <LoaderApp v-if="isLoading"/>
    <div
      :style="{ backgroundImage: cFonUrl}"
      class="mainLayout"
      v-else
      sticky-container
    >
      <Navbar v-if="$store.state.userId" v-sticky sticky-offset="{top:0}"/>
      <main class="mainContainer">
        <router-view/>
      </main>
      <Msg/>
      <MsgFB/>
      <HypSide/>
      <AlarmPopupList v-if="!$store.state.isAK"/>

      <div class="upButton hover" :class="{'hide':get_scroll_position()<=400}">
<!--      <div class="upButton hover" :class="{'hide':$store.state.scrollPosition<=400}">-->
        <img
          src="/img/up2.png"
          alt="up button"
          @click="up"
        />
      </div>
    </div>
  </div>

</template>

<script>
  import "../mixins/scrollPosition.mixin";
  import Sidebar from '../components/app/Sidebar'
  import Navbar from '../components/app/Navbar'
  import AlarmPopupList from '../components/Alarm/AlarmPopupList';
  import HypSide from '../components/HypSide';
  import LoaderApp from '../components/LoaderApp';
  export default {
    name: 'main-layout',
    components: { LoaderApp, HypSide, AlarmPopupList, Navbar, Sidebar},
    data:()=>({
      isLoading:true,
      intervalGecko:null,
      intervalGlobal:null,
    }),
    methods: {
      up(){
        window.scrollTo({top: 0, behavior: 'smooth'});
      },
    },
    computed:{
      cFonUrl(){
        if(this.$store.state.reserveFonUrl){
          return this.$store.state.reserveFonUrl
        }else return this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'].service.fonUrl
      }
    },
    async mounted() {
        await this.$store.dispatch('fetchFBInfo');//загр свои данные с FB.
        // await this.$store.dispatch('FBFetchParticularList',{listName:'tradeList'});//загр свои данные с FB.
        // await this.$store.dispatch('FBFetchParticularList',{listName:'historyList'});//загр свои данные с FB.
        await this.$store.dispatch('loadGlobal');//загр глоб данные.
        this.intervalGlobal = setInterval(()=>{
          this.$store.dispatch('loadGlobal');//загр глоб данные, редко.
        },this.$store.state.infoMain.service.refreshGlobalInterval);//рефреш базы в интервале указанном в стейте (2+ часа)
        await this.$store.dispatch('loadAllGeckoCoins');//загр имен всех альтов, около 9000шт, краткое инфо.
        await this.$store.dispatch('loadMyGeckoCoins');//загр моих альтов с Геко.
        this.intervalGecko = setInterval(()=>{
          this.$store.dispatch('loadMyGeckoCoins');
        },this.$store.state.infoMain.service.refreshCoinsInterval);//рефреш базы в интервале 2мин
        this.isLoading = false

      // //рендер в зависимости от экрана
      // const mediaQuery = window.matchMedia("(min-width:768px)");
      // this.showMobileMenu = mediaQuery.matches;
      // const listener = e => this.showMobileMenu = e.matches;
      // mediaQuery.addListener(listener);
      // this.$once('hook:beforeDestroy', () => mediaQuery.removeListener(listener));

    },
    beforeDestroy () {//очистить обновление loadMyGeckoCoins при logout
      clearInterval(this.intervalGecko)
      clearInterval(this.intervalGlobal)
    }
  }
</script>

<style scoped>
  .mainLayout {
    background-size: cover;
    min-height: 100vh;
    background-attachment:fixed;
    background-position: center top;
  }
  .mainContainer {
    max-width:1600px;
    margin:0 auto ;
    padding: 50px 0;
  }
  .upButton{
    position: fixed;
    bottom:50px;
    right:20px;
    width:40px;
    height:40px;
  }
  .hide{
    display: none;
  }
  .upButton img{
    width:100%;
