export default function numberReadableFormat(value){
  // if (typeof(value) === 'number'){
  //   return new Intl.NumberFormat().format(value);//100,000,000.00
  // }else{
  //   console.log(typeof(value))
  //   return `не число`
  // }

  if (typeof(value) !== 'number'){
    return `не число`
  }
  if(value <0.01){
    return +value.toExponential(2)
  }
  if(value >0.01 && value <1){
    return +value.toFixed(3)
  }
  if(value >1 && value <100){
    return +value.toFixed(2)
  }
  else {
    return +value.toFixed(0);
  }
// else{
//     number = number.toFixed(0)
//     return new Intl.NumberFormat().format(Number(number));//100,000,000
//   }
}


