<template>
  <nav class="navContainer">
    <div class="menu hideDesk" @click="toOpen">&equiv;</div>
    <span class="logo hideMob hover" @click="$store.dispatch('loadMyGeckoCoins')">MyCrypto</span>
    <LoaderCountdown
      :is-loading="$store.state.isLoading"
      :interval="$store.state.infoMain.service.refreshCoinsInterval/1000"
      class="loaderCountdown"
    />
    <div class="switchContainer">
      <toggle-button
        :labels="{checked: cChecked, unchecked: cUnchecked}"
        :color="{checked: 'green', unchecked: 'rgba(0,0,0,0.5)'}"
        @change="$store.commit('switchAK')"
        style="border:1px solid grey; border-radius: 12px"
      />
    </div>

    <router-link v-for="item in computedNavItemsArr" :to="item.linkName" class="menuItem hoverLight hideMob mr20" exact-active-class="active">
      {{item.title}}
    </router-link>

    <router-link to="notes" class="notesButton hover">
      <img src="/img/notes.png" alt="notes" style='width:100%'/>
    </router-link>

    <router-link to="cosmo" class="cosmoButton hover">
      <img src="/img/cosmo.png" alt="cosmo" style='width:100%'/>
    </router-link>

    <router-link to="/" class="homeButton hover hideDesk" >
      <img src="/img/home.png" alt="cosmo" style='width:100%'/>
    </router-link>

    <div class="hoverLight fz12 w ml20 hideMob" style="padding-top:5px"  @click="logout">Logout</div>
    <div class="timeContainer hideMob">
      <span>San Francisco: {{dateSF}}</span>
      <br/>
      <span>Moscow: {{dateMS}}</span>
      <span class="timeDrop">
        <br/>
        <span>EST: {{dateEST}}</span>
        <br/>
        <span>UTC: {{dateUTC}}</span>
      </span>
    </div>

    <Transition name="slide-fade">
      <div class="menuContainer card" v-if="isMenuOpen"  v-click-outside="onClickOutside">
        <router-link v-for="item in computedNavItemsArr" :to="item.linkName" class="menuItemMob hoverLight" style="margin-bottom: 10px;" @click="isMenuOpen=false">
          {{item.title}}
        </router-link>
        <div class="logout hoverLight" @click="logout">Logout</div>
      </div>
    </Transition>
  </nav>
</template>

<script>
  import clickOutside from '../../directives/clickOutside'
  import HypSide from '../HypSide';
  import LoaderCountdown from '../LoaderCountdown';
  import AlarmPopupList from '../Alarm/AlarmPopupList';
  export default {
    components: { AlarmPopupList, LoaderCountdown, HypSide},
    directives: {clickOutside},
    data: () => ({
      dateSF: null,
      dateMS: null,
      dateEST: null,
      dateUTC: null,
      intervalAlarm:null,
      interval:null,//in mounted we record this interval
      dropdown:null,
      isMenuOpen:false,
      isMenuOpenByButton:false,//фикс закрывания сразу
    }),
    methods:{
      logout(){
        this.$store.dispatch('logout')
        this.$router.push('/login')
      },
      toOpen(){
        this.isMenuOpen = !this.isMenuOpen
        this.isMenuOpenByButton = true//фикс закрывания сразу
      },
      toClose(){
        this.isMenuOpen = false
        this.isMenuOpenByButton = false
      },
      onClickOutside (event) {
        if(this.isMenuOpen && !this.isMenuOpenByButton){
          return this.isMenuOpen = !this.isMenuOpen;
        }
        this.isMenuOpenByButton = false//фикс закрывания сразу. Возврат в первонач состояние.
      },
    },
    computed:{
      computedNavItemsArr(){
        return [
          {title:'Main', linkName:'/'},
          {title:'Alarm', linkName:'alarm'},
          {title:'Potential', linkName:'potential'},
          {title:'Fund', linkName:'fund'},
          // {title:'History', linkName:'history'},
          {title:'Weight', linkName:'weight'},
          {title:'Trash', linkName:'trash'},
          {title:'Hyp', linkName:'hyp'},
          {title:'Data', linkName:'raredata'},
          {title:this.$store.state.userId[0]==='4'?'Lab':'', linkName:'lab'},
          {title:this.$store.state.userId[0]==='4'?'Resume':'', linkName:'resume'},
          {title:'Beloe', linkName:'beloe'},
        ]
      },
      cChecked(){
        return this.$store.state.userId[0]==='4'?'K':'A'
      },
      cUnchecked(){
        return this.$store.state.userId[0]==='4'?'A':'K'
      },
    },
    mounted () {
      this.interval = setInterval(()=>{
        this.dateSF= new Date().toLocaleString("ru-Ru", {timeZone: "America/los_angeles"})
        this.dateMS= new Date().toLocaleString("ru-RU", {timeZone: "Europe/Moscow"})
        this.dateEST= new Date().toLocaleString("ru-RU", {timeZone: "EST"})
        this.dateUTC= new Date().toLocaleString("ru-RU", {timeZone: "UTC"})
      },1000)

      // this.dropdown = M.Dropdown.init(this.$refs.dropdown,{
      //   constrainWidth:true
      // })
    },
    watch: {//при ручном изменении урла срабатывает рендер. При первом рендере и при послед изменениях.
      '$route.name': {
        handler(){//завод опция "что сделать"
          this.toClose();
        },
        immediate:false,//= метод created() сработает с таким же содержимым (при первом рендере). Можно удалить оригинальный метод created() .
      },
    },
    beforeDestroy () {//очистить обновление времени из памяти при исчезновении панели Navbar
      clearInterval(this.interval)
    }
  }
</script>
